@import "../../../common/styles/globals/colors";
@import "../../../common/styles/globals/mixins";
@import "../../../common/styles/globals/forms";

.authority-details-modal {

  .authority-details {
    position:relative;

    .loading-mask {
      position:absolute;
      background-color:rgba(255, 255, 255, 0.9);
      top:0;
      right:0;
      bottom:0;
      left:0;
      display:flex;
      flex-direction:column;
      justify-content:center;
      text-align:center;
    }
  }

  .modal-header h1.title, label, form.crud input {
    font-size:15px;
  }

  #file_name, #color { width:220px; }

  .color {

    dd { position:relative; }

    .color-sample {
      position:absolute;
      top:9px;
      right:-33px;
      display:inline-block;
      width:27px;
      height:27px;
      background-color:$form-grey-border;
      cursor:pointer;

      @include rounded-corners(4px);

    }

    .swatches-picker {
      position:absolute;
    }

  }

  .btn.add-file {
    padding:7px 35px;
    vertical-align:baseline;
  }

  .footer {
    padding:10px;

    button {
      margin:0 5px;
    }

    button.delete {
      float:left;
    }
  }

}